/** Letter */

$__tracking-map: (
  "tighter": -0.05em,
  "tight": -0.025em,
  "normal": 0,
  "wide": 0.025em,
  "wider": 0.05em,
  "widest": 0.1em
);

/**
 * Sets the tracking space.
 */
@each $__tracking, $__tracking-value in $__tracking-map {
  .tracking-#{$__tracking} {
    letter-spacing: $__tracking-value;
  }
}
